/* stylelint-disable */

@mixin desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin small-desktop {
  @media (max-width: (1280px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin small-mobile {
  @media (max-width: (476px)) {
    @content;
  }
}

@mixin xs-mobile {
  @media (max-width: (376px)) {
    @content;
  }
}
